@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@300&display=swap');

body {
  font-family: 'Noto Sans JP', sans-serif;
  background-color: #f6f6f6;
}

h3 {
  font-weight: bold;
  text-align: center;
}

.match {
  background-color: white;
  width: 808px;
  width: 900px;
  margin-left: auto;
  margin-right: auto;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

table {
  width: 100%;
  padding: 20px;
}

table td {
  font-size: 16px;    
}    

.teams td {
  text-align: center;
  font-weight: bold;
  font-size: 20px;
  width: 50%;
}

.home {
  width: 40%;
  text-align: right;
}

.number {
  width: 20%;
  text-align: center;
}

.away { 
  width: 40%;
}

img {
  width: 65px;
  height: 65px;
}

hr {
  border-top: 1px solid #adadad;
}

.checkbox {
  position: fixed;
  left: 150px;
  top: 50px;
}

form label input {
  margin-right: 10px;
  margin-bottom: 10px;
}

.loadingData {
  text-align: center;
}

.spinner.loading {
  border: none;
  position: relative;
  text-align: center;
}

.spinner.loading:before {
  content: "";
  height: 20px;
  width: 20px;
  position: absolute;
  border-width: 4px;
  border-style: solid;
  border-color: #2180c0 #fff #fff;
  border-radius: 100%;
  animation: rotation .7s infinite linear;
}

@keyframes rotation {
  from {
    transform: rotate(0deg);
  } to {
    transform: rotate(359deg);
  }
}